<template>
  <div class="sidebar">
    <TableSelector @setTable="set_table" class="header-item"/>
    <DateSelector @getDate="update_date" class="header-item"/>
    <FilterPreset :filter="filter" class="header-item"/>
    <SongFilter :filter="filter" class="header-item"/>
    <DetailColumns :filter="filter" class="header-item"/>
  </div>
</template>

<script>
import TableSelector from "./TableSelector";
import DateSelector from "./DateSelector";
import SongFilter from "./SongFilter";
import DetailColumns from "./DetailColumns";
import FilterPreset from "./FilterPreset";
import Filter from "../../models/filter.js"

export default {
  name: "Sidebar",
  components: {DateSelector, DetailColumns, TableSelector, SongFilter, FilterPreset},
  props: {
    filter: {
      type: Filter,
      required: true,
    }
  },
  methods: {
    set_table(table) {
      this.$emit('setTable', table);
    },
    update_date(date) {
      this.$emit('getDate', date);
    },
  }
}

</script>

<style scoped>
.sidebar {
  background-color: #191970;
  color: #ffffff;
  height: 100%; /*  */
  width: 320px; /* width */
  max-width: 320px; /* width */
  opacity: 0.95; /*  0 */
  position: fixed; /* () */
  overflow-x: hidden; /*  */
  box-sizing: border-box; /* paddingborderwidthheight */
}

.header-item {
  padding: 40px 40px 0 40px;
}

.sidebar-link-area {
  padding-top: 20px; /*  */
}

.sidebar-link {
  color: #ffffff; /*  */
}

.sidebar-link:hover {
  color: #ffffff /*  */
}
</style>